@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
:root{--bg-color-1: radial-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%))}html,body{font-family:"Barlow Semi Condensed", sans-serif;font-size:24px}body{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:radial-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%));background:var(--bg-color-1);background-repeat:no-repeat;background-size:cover;padding:0;margin:0;width:100vw;overflow-x:hidden;height:100%;min-height:100vh}body *{box-sizing:border-box}

.app{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;width:100%;height:100%;place-items:center;padding:calc(1vw + 0.5rem)}

.app-scoreBoard{width:100%;margin:0.2rem}.scoreBoard-content{background:transparent;display:grid;grid-template-columns:50% 50%;border:1px solid #606e85;border-radius:15px;position:relative;grid-gap:calc(10% - 0.8rem);gap:calc(10% - 0.8rem);place-items:center;padding:1rem;margin:0.2rem}.score-content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background:white;width:calc(30% + 0.8rem);min-width:100px;height:100px;border-radius:10px}.score-content .title{font-size:0.8em;color:#2a46c0;text-transform:lowercase}.score-content .value{font-size:2em;color:#606e85;font-weight:700}.list{padding:0;margin:0;list-style:none;color:white;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;max-height:80%}.list .list-item{padding:0;margin:0;font-size:1.2rem;text-transform:uppercase;line-height:0.8em}@media only screen and (min-width: 1000px){.app-scoreBoard{width:50%}}

.btn{position:relative;width:calc(1vw + 6rem);height:calc(1vw + 6rem)}.btn.clicked .box-1 .box-2{-webkit-transform:translateY(-5%);transform:translateY(-5%)}.box-1{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:100%;height:100%;border-radius:50%;-webkit-transform:perspective(300px) rotateX(10deg);transform:perspective(300px) rotateX(10deg);outline:0;border:0;margin:0;padding:0;cursor:pointer}.box-2{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:100%;height:100%;border-radius:50%;background-color:#5671f5;-webkit-transform:translateY(-10px);transform:translateY(-10px);overflow:hidden}.box-1.rock{background-color:#dc2e4e}.box-1.rock .box-2{background-color:#dd405d}.box-1.paper{background-color:#4865f4}.box-1.paper .box-2{background-color:#5671f5}.box-1.scissors{background-color:#ec9e0e}.box-1.scissors .box-2{background-color:#eca922}.box-1.lizard{background-color:#834fe3}.box-1.lizard .box-2{background-color:#8c5de5}.box-1.cyan{background-color:#40b9ce}.box-1.cyan .box-2{background-color:#52bed1}.box-3{display:grid;place-items:center;width:calc(100% - 30px);height:calc(100% - 30px);border-radius:50%;background-color:#919191;overflow:hidden}.box-4{display:grid;place-items:center;width:100%;height:100%;border-radius:50%;background-color:white;-webkit-transform:translateY(8px);transform:translateY(8px)}.box-5{display:grid;place-items:center;width:calc(100% - 10px);height:calc(100% - 10px);border-radius:50%;background-color:rgba(219,219,219,0.1)}

.app-body{display:grid;place-items:center;width:100vw;height:500px}.app-options{position:relative;width:100%;max-width:500px;height:100%;padding:10px}.option-btn{position:absolute}.option-btn.btn-1{left:5%;top:6%}.option-btn.btn-2{right:5%;top:6%}.option-btn.btn-3{bottom:10%;left:calc(50% - 0.5vw - 3rem)}.option-connections{position:relative;height:100%;width:100%}.connections{position:absolute;left:0;top:0;width:100%;height:100%;display:grid;place-items:center}

.rules-btn{background:transparent;width:calc(25vw + 0.5rem);height:calc(4vh + 0.5rem);border-radius:10px;border:1px solid white;color:white;font-size:0.6em;text-transform:uppercase;font-weight:400;letter-spacing:0.1rem}

.shadow-content{--width: 50vw;--board: 8vw;background-color:#dbc0c0;width:var(--width);height:var(--width);display:grid;place-items:center;border-radius:50%;position:absolute;z-index:-1000;-webkit-transform:translateX(-8vw) translateY(-8vw);transform:translateX(-8vw) translateY(-8vw)}.shadow-content .shadow-1{background-color:rgba(252,252,252,0.05);width:calc(var(--width) - var(--board));height:calc(var(--width) - var(--board));display:grid;place-items:center;border-radius:50%}.shadow-content .shadow-1 .shadow-2{background-color:rgba(252,252,252,0.05);width:calc(calc(var(--width) - calc(var(--board) * 2)) + 0);height:calc(calc(var(--width) - calc(var(--board) * 2)) + 0);display:grid;place-items:center;border-radius:50%}.shadow-content .shadow-1 .shadow-2 .shadow-3{background-color:rgba(252,252,252,0.05);width:calc(calc(var(--width) - calc(var(--board) * 3)) + 0);height:calc(calc(var(--width) - calc(var(--board) * 3)) + 0);display:grid;place-items:center;border-radius:50%}

.vs-board{display:grid;width:100%;grid-template-rows:50vh 30vh}.vs-board .body{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:100vw;color:white;font-size:0.9em;padding:0.5rem}.vs-board .body .player{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;grid-gap:1rem;gap:1rem;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:50vw}.vs-board .body .player .options-btn{display:grid;place-items:center;padding:0.5rem;width:100%;height:50%}.vs-board .body .play-again-btn{text-transform:uppercase;font-size:0.5em;color:black;width:10em;border-radius:6px;outline:0;border:0;height:2.5em;cursor:pointer}.vs-board .body .play-again-btn:hover{color:red}.vs-board .body .computer{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;grid-gap:1rem;gap:1rem;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:50vw}.vs-board .body .computer .options-btn{display:grid;place-items:center;width:100%;height:50%}.vs-board .body .computer .options-btn .empty-btn{display:block;width:100px;height:100px;background-color:rgba(0,0,0,0.2);border-radius:50%}.vs-board .body .shadow-circle{display:block;background-color:#fff;border-radius:50%;position:absolute}.status-component{display:grid;place-items:center;grid-template-rows:0.2fr 1fr}.status-component .title{padding:0;margin:0;font-size:2.5rem;text-transform:uppercase;color:white}.status-component .play-again-btn{width:calc(50vw + 1rem);height:calc(3vh + 1.5rem);font-size:0.8rem;font-weight:700;word-spacing:0.2rem;text-transform:uppercase;border-radius:15px;color:#1f3756;background:white;border:0}

.rules-preview-content{top:0;left:0;width:100vw;height:100%;position:fixed;z-index:10000;background-color:white;display:grid;grid-template-columns:100%;grid-template-rows:20vh 60vh 15vh}.rules-preview-content .title{height:15vh;padding:0;margin:0;display:grid;place-items:center}.rules-preview-content .model-content{text-align:center;width:100%;height:100%}.rules-preview-content .model-content .model-img{width:80%;height:80%;background-position:center;background-size:contain}.rules-preview-content .btn-content{text-align:center;height:15vh;padding:0;margin:0}.rules-preview-content .btn-content .btn{height:100%;max-height:calc(10vh + 0.2rem);background:transparent;border:0;outline:0}

